<h3 style="text-align: center"><a target="_blank"
 href="https://smartscan.cash/address/{CoinAddress}">{Symbol}</a> Pool</h3>

<div>
  <TabBar tabs={['BUY', 'POOL', 'FAQ']} let:tab bind:active>
    <Tab {tab} >
      <Label><span style="font-size: 20px">{tab}</span></Label>
    </Tab>
  </TabBar>
</div>

{#if active=='BUY'}
<div>
  <p><Textfield bind:value={Multiplier} label="Multiplier"  on:input={()=>(syncValues(true))}
  style="width: 130px"></Textfield>&nbsp;(1.05~1000)<br>
  <span style="color: grey; font-size: 16px">If you win, the coins paid by you will be multiplied by this value and returned to you.</span></p>

  <p>When the multiplier equals {Multiplier}, the maximum amount you can buy is {MaxAmount}, and your chance to win is {Chance}%.

  <p><Textfield bind:value={Amount} label="Amount" on:input={()=>(syncValues(true))}
  style="width: 130px"></Textfield><span style="color:red">{AmountWarn}</span><br>
  <span style="color: grey; font-size: 16px">How many {Symbol} do you want to paid for this lottery ticket?</span></p>
  
  <p>The fee you paid for this lottery is {FeeAmount}{Symbol}. If you win, the reward is {RewardAmount}{Symbol}.</p>

  {#if CoinIsCashCats}
  <p>After buying this lottery, you can get {Amount} CATSUP.</p>
  {/if}

  <Button on:click={buy} variant="outlined">
    <Label style="font-size: 18px">Buy Lottery!</Label>
  </Button>

  <hr>

  <p><Select bind:value={FilterMethod} on:click={onFilterMethodChange} label="Show Lotteries from">
      <Option value="All">All Addresses</Option>
      <Option value="Mine">My Addresses</Option>
      <Option value="Specified">A Specified Address</Option>
  </Select></p>

  <p>
  <Button on:click={()=>(OpenRecCountDlg=true)} variant="outlined">
    <Label style="font-size: 18px">Show at most {RecCountLimit} records</Label>
  </Button>
  </p>

  <p>
  <Button on:click={showAndUpdateHistoryTable} variant="outlined">
    <Label style="font-size: 18px"><img style="width: 15px; height: auto; vertical-align: sub" src="data:image/svg+xml;base64,">&nbsp;Refresh</Label>
  </Button>
  </p>

  {#if FilterMethod != "All"}
    <p>Addr: {SpecifiedAddr}</p>
    {#if FilterMethod == "Mine"}
    <p>Total Won / Total Bought = {TotalWon} / {TotalBought} = {ROI}%</p>
    {/if}
  {/if}

  {#if ShowHistoryTable}
    <table>
    <tr><th>Time</th><th>Balance</th><th>Multiplier</th><th>txid</th></tr>
    {#each HistoryEntries as entry (entry.id)}
      <tr><td>{entry.timeStr}</td>
      {#if entry.isWin}
        {#if entry.amount >= TotalCoins*0.01*0.5}
           <td class="neonText" style="font-weight: bold">+{entry.amount}</td><td>{entry.multiplier}</td>
        {:else}
           <td style="color: green">+{entry.amount}</td><td>{entry.multiplier}</td>
        {/if}
      {:else if entry.isBuy}
        <td style="color: red">-{entry.amount}</td><td>{entry.multiplier}</td>
      {/if}
      <td><a target="_blank" href="https://smartscan.cash/transaction/{entry.transactionHash}">view</a></td></tr>
    {/each}
    </table>
  {/if}
</div>
{/if}
 
{#if active=='POOL'}
<div>
  <p>Current address: {SignerAddr}</p>
  <p>You can deposit {Symbol} into this lottery pool and earn fees. But if lottery buyers win their rewards, you will suffer some loss together with the pool.</p>
  <p>Now the lottery pool has {TotalShare} shares and each share equals {CoinPerShare}{Symbol} (totally {TotalCoins}{Symbol}). You have {UserShare} shares in the pool (equaling {UserCoins}{Symbol}). You can get more share by depositing {Symbol} into the pool, and your shares will be burnt after withdrawing from the pool.<br>
Your shares can be transferred to others as an SEP token <a target="_blank"
 href="https://smartscan.cash/address/{PoolAddress}">clk{Symbol}.</a>
Please let your wallet <a href="#" on:click|preventDefault={watchClkToken}>watch clk{Symbol}</a>.
</p>

