    <p><b>Who's behind Catsluck?</b></p>
    <p>We are supporters of <a target="_blank" href="https://cashcats.org">cashcats</a>. People often ask what can we do with $CATS? So catsluck was born. You can bet and invest with $CATS now.</p>

    <p><b>Can I get reward as a referer?</b></p>
    <p>Yes. Please send some catsluck.com URLs containing your ReferID to potential players. If a player click the URLs and start to bet with a token, then in his lifetime 0.25% of the token he ever pays for catsluck lotteries will be rewarded to you.</p>
    {#if ReferID!=0}
    <p>You have already registered as a referer. Your ReferID is {ReferID}. Please send one of the following URLs to potential players:<br>
    <a target="_blank" href="https://catsluck.com/?r={ReferID}">https://catsluck.com/?r={ReferID}</a><br>
    <a target="_blank" href="https://catsluck.com/pool/cats?r={ReferID}">https://catsluck.com/pool/cats?r={ReferID}</a><br>
    <a target="_blank" href="https://catsluck.com/pool/bch?r={ReferID}">https://catsluck.com/pool/bch?r={ReferID}</a><br>
    <a target="_blank" href="https://catsluck.com/pool/token?c={Symbol.replace('$','')}&r={ReferID}">https://catsluck.com/pool/token?c={Symbol.replace('$','')}&r={ReferID}</a><br>
    In this pool, you have accumulated {ReferReward}{Symbol} reward. Please <a href="#" on:click|preventDefault={getRefererReward}>click here</a> to get the reward.
    </p>
    {:else}
    <p>Please <a href="#" on:click|preventDefault={registerAsReferer}>register as a referer</a> to get a ReferID.</p>
    {/if}

    {#if !CoinIsCLK}
    <p><b>How to buy back CLK for burning?</b></p>
    <p>Every time someone bets, 0.5% of the betting {Symbol} is accumulated for buyback auction.</p>
    {#if DuringAuction}
      <p>Currently, a Dutch auction is running. You can <a href="#" on:click|preventDefault={endAuction}>end the auction</a> by paying {ClkOfAuction} CLK to get {CoinOfAuction}{Symbol} now (price={PriceOfAuction}). The CLK paid by you will be burnt by the smart contract.</p>
    {:else}
      <p>There have been {CoinReadyForAuction}{Symbol} accumulated for a Dutch auction. You can <a href="#" on:click|preventDefault={startAuction}>send a transaction</a> to start the auction, which will sell these {Symbol} in exchange for CLK.</p>
    {/if}
    {:else}
    <p><b>How to buy back CLK for burning?</b></p>
    <p>Every time someone bets, 0.5% of the betting CLK is burnt automatically.</p>
    {/if}

    <p><b>What is CLK?</b></p>
    <p>CLK's address: {ClkAddress}</p>
    <p>CLK is catsluck's exclusive token. CLK is a deflation coin now. Before <b>Tue, 01 Mar 2022 00:00:00 GMT</b>, users could get 1 free CLK every time he betted 1 $CATS. After that, the total amount of CLK does not increase. Till now, there are totally {ClkSupply} CLK in circulation. Please let your wallet <a on:click|preventDefault={watchCLK}>watch CLK</a>.</p>

    <p><b>Did the development team reserve CLK?</b></p>
    <p>Not at all. Absolutely fair distribution.</p>

    <p><b>What is CATSUP?</b></p>
    <p>CATSUP is token with infinite supply. Its address: {CatsupAddress}</p>
    <p>With the new version of $CATS pool, users can get 1 free CATSUP every time he bets 1 $CATS. There is no end time for minting CATSUP.</p>
    <p>CATSUP will be used as an expandable token for a sub-project, which will go live later.</p>
    <p>Please let your wallet <a href="#" on:click|preventDefault={watchCATSUP}>watch CATSUP</a>.</p>

<!--
    <p><b>What is FUN?</b></p>
    <p>FUN is another token with infinite supply. Its address: {FunAddress}</p>
    <p>If you are a $CATS holder, you can click <a on:click|preventDefault={mintFUN}>here</a> to mint FUN tokens which equal your $CATS balance.</p>
    <p>Please let your wallet <a href="#" on:click|preventDefault={watchFUN}>watch FUN</a>.</p>
-->

    <p><b>What is house edge?</b></p>
    <p>The house edge is 2.5%.</p>
    {#if CoinIsCLK}
    <p>1.75% is allocated to reward pool, 0.5% is burnt, and 0.25% goes to the referers.</p>
    {:else}
    <p>1.75% is allocated to reward pool, 0.5% is used to buy back CLK in circulation for burning, and 0.25% goes to the referers.</p>
    {/if}

    <p><b>Is catsluck open source?</b></p>
    <p>Yes. Check it here <a href="https://github.com/catsluck/catsluck">https://github.com/catsluck/catsluck</a>.</p>

    <p><b>Where can I find help?</b></p>
    <p>Some users set up these channels:</p>
    <p><a target="_blank" href="https://t.me/catsluck_chinese">https://t.me/catsluck_chinese</a></p>
    <p><a target="_blank" href="https://t.me/catsluck_english">https://t.me/catsluck_english</a></p>
    <p>You can try to ask question there. But the lazy developers are not in them.</p>
</div>
{/if}
 
