async function connectWeb3() {
  if (typeof window.ethereum === 'undefined') {
    if (typeof window.web3 !== 'undefined') {
      window.ethereum = window.web3;
    } else if (typeof window.TPJSBrigeClient !== 'undefined') {
      window.ethereum = window.TPJSBrigeClient;
    } else if (typeof window.imToken !== 'undefined') {
      window.ethereum = window.imToken;
    } else {
      const provider = await detectEthereumProvider();
      if (provider) {
        window.ethereum = provider;
      } else if(IsPC()) {
        myAlert("Your browser has not installed a wallet extension (like MetaMask).")
        return false;
      } else {
        myAlert("Please open this page inside a mobile wallet App.")
        return false;
      }
    }
  }
  window.accounts = await window.ethereum.request({method: 'eth_requestAccounts'});
  if (window.accounts.length == 0) {
    myAlert("Cannot connect to wallet.")
    return false;
  }
  const chainId = await window.ethereum.request({method: 'eth_chainId'})
  if(chainId != SmartBCHChainId) {
    myAlert("You are not in smartBCH network.", chainId)
    return false;
  }
  return true;
}

function sleep(ms) {
  return new Promise(resolve => setTimeout(resolve, ms));
}

function formatTime(timestamp) {
  var date = new Date()
  date.setTime(timestamp)
  return date.toLocaleString('en-US', {
    month: 'numeric',
    day: 'numeric',
    hour: 'numeric',
    minute: 'numeric',
    hour12: false,
  }).replace("24:", "00:")
}

function escapeHTML(s) {
  return s.replace(/&/g,'&amp;').replace(/</g,'&lt;').replace(/>/g,'&gt;');
}

function myFormatUnits(v, digits, displayDigits) {
	if(!displayDigits) displayDigits = DisplayDigits;
	var n = ethers.utils.formatUnits(v, digits)*1.0;
	return n.toFixed(displayDigits)*1.0;
}

async function syncValues(autoFillAmount) {
  const div = (Multiplier < 3) ? 200.0 : 100.0
