  //console.log("MaxAmount", MaxAmount)
  AmountWarn = ""
  if(Amount > MaxAmount) {
    if(autoFillAmount) {
      Amount = MaxAmount
    } else {
      AmountWarn = "> "+MaxAmount
    }
  }
  var chance = 100.0/Multiplier
  if(chance < 1) {
    chance = chance.toFixed(4)
  } else if(chance < 10) {
    chance = chance.toFixed(3)
  } else {
    chance = chance.toFixed(2)
  }
  Chance = chance;
  FeeAmount = (Amount*0.025).toFixed(DisplayDigits)*1.0
  RewardAmount = ((Amount-FeeAmount)*Multiplier).toFixed(DisplayDigits)*1.0

  const provider = new ethers.providers.Web3Provider(window.ethereum)
  const clkContract = new ethers.Contract(ClkAddress, Erc20ABI, provider)

  var totalBurnt = await clkContract.balanceOf(BurnAddress)
