	}
}

async function fetchWithTimeout(resource, options = {}) {
  const { timeout = 3000 } = options;
  
  const abortController = new AbortController();
  const id = setTimeout(() => abortController.abort(), timeout);
  const response = await fetch(resource, {
    ...options,
    signal: abortController.signal  
  });
  clearTimeout(id);
  return response;
}

async function getBetaAndPI(hashBN) {
  const hashHex = hashBN.toHexString().substr(2)
  const servers = ["https://enclave0.vrf.cash:8081", "https://enclave1.vrf.cash:8081"];
  var idx = (Math.random() > 0.5) ? 1 : 0;
  for(var i=idx; i<100; i++) {
    var j = i%2;
    try {
      //console.log("server", servers[j], j)
      const response = await fetchWithTimeout(servers[j]+"/vrf?b="+hashHex)
      //console.log("server", servers[j]);
      var result = await response.json();
      //console.log(result);
      if(result.PI) {
        return result;
      }
    } catch(e) {
      continue
    }
  }
}

async function queryMyReward(poolContract) {
	//console.log("HH1");
	var [height, hash] = await poolContract.getLastTicketHeightAndHash();
	//console.log("HH2");
	if(height == 0) return ethers.BigNumber.from(0);
	if(hash.isZero()) return AllOnes;
	var res = await getBetaAndPI(hash);
	var reward = await poolContract.callStatic.getMyReward("0x"+res.Beta, "0x"+res.PI);
	return reward;
}

