async function buy() {
  syncValues(false)
  const provider = new ethers.providers.Web3Provider(window.ethereum)
  const checkResult = await checkVrfServer(provider);
  if(!checkResult) {
    WarnText = "Cannot connect to the VRF server. Failed to buy lottery.";
    OpenWarnDlg = true;
    return;
  }
  const signer = provider.getSigner()
  const tokenContract = new ethers.Contract(CoinAddress, Erc20ABI, provider).connect(signer)
  if(Amount > MaxAmount) {
    WarnText = `The amount ${Amount} is larger than the maximum amount you can buy (${MaxAmount})!`
    OpenWarnDlg = true
    return
  }
  const signerAddr = await signer.getAddress()
  const balanceBN = await tokenContract.balanceOf(signerAddr)
  const balance = myFormatUnits(balanceBN, CoinDecimal)
  if(balance < Amount) {
    WarnText = `You balance (${balance}) is less than the amount (${Amount}) you want to pay!`
    OpenWarnDlg = true
    return
  }
  if(CoinAddress != BchAddress) {
    const allowanceBN = await tokenContract.allowance(signerAddr, PoolAddress)
    const allowance = myFormatUnits(allowanceBN, CoinDecimal)
    //console.log("HH", allowance, Amount, CoinAddress, allowanceBN)
    if(allowance < Amount) {
      HintText = "You have not approved enough allowance to this lottery contract. Please appove and retry buying."
      ApprovingAddress = CoinAddress
      OpenApproveDlg = true
      return
    }
  }
  const catsluckContract = new ethers.Contract(PoolAddress, ABI, provider).connect(signer)
  const amountBN = ethers.utils.parseUnits((Amount*1.0).toFixed(CoinDecimal), CoinDecimal)
  const multiplierX100 = (Multiplier*100).toFixed(0)
  if(multiplierX100 < 105 || multiplierX100 > 100000) {
    WarnText = "The multiplier is invalid. It must be within the range [1.05, 1000]."
    OpenWarnDlg = true
    return
  }
  var referer = localStorage.getItem("referer");
  if(!referer) {
    referer = ZeroAddress;
  }
  var args = {gasPrice: DefaultGasPrice}
  if(CoinAddress == BchAddress) {
    args["value"] = amountBN
  }
  //console.log("referer", referer)
  OpenWaitDialog = true
  //console.log("HHH", amountBN)
  const tx = await catsluckContract.buyLottery(amountBN, multiplierX100, referer, args)
  await tx.wait()
  const poolContract = new ethers.Contract(PoolAddress, ABI, provider).connect(signer)
  var reward = await queryMyReward(poolContract)
  while(reward.eq(AllOnes)) { // wait until the block hash is available
    await sleep(2000)
    reward = await queryMyReward(poolContract)
  }
  OpenWaitDialog = false
  if(reward.isZero()) {
    OpenNoRewardDlg = true
    return
  }
  OpenYesRewardDlg = true
  return
}

async function deposit() {
  const provider = new ethers.providers.Web3Provider(window.ethereum)
  const signer = provider.getSigner()
  const tokenContract = new ethers.Contract(CoinAddress, Erc20ABI, provider).connect(signer)
  const signerAddr = await signer.getAddress()
  const balanceBN = await tokenContract.balanceOf(signerAddr)
  const balance = myFormatUnits(balanceBN, CoinDecimal)
  //console.log("balance", balance)
  if(balance < DepositAmount) {
    WarnText = `You balance (${balance}) is less than the amount (${DepositAmount}) you want to deposit!`
    OpenWarnDlg = true
    return
  }
  if(CoinAddress != BchAddress) {
    const allowanceBN = await tokenContract.allowance(signerAddr, PoolAddress)
    const allowance = myFormatUnits(allowanceBN, CoinDecimal)
    //console.log("allowance", allowance)
    if(allowance < DepositAmount) {
      HintText = "You have not approved enough allowance to this lottery contract. Please appove and retry to deposit."
      ApprovingAddress = CoinAddress
      OpenApproveDlg = true
      return
    }
  }
  const poolContract = new ethers.Contract(PoolAddress, ABI, provider).connect(signer)
  const depositAmountBN = ethers.utils.parseUnits((DepositAmount*1.0).toFixed(CoinDecimal), CoinDecimal)
  //console.log("DepositAmount", DepositAmount, CoinDecimal, depositAmountBN.toHexString())
  var args = {gasPrice: DefaultGasPrice}
  if(CoinAddress == BchAddress) {
    args["value"] = depositAmountBN
  }
  OpenWaitDialog = true
  const tx = await poolContract.deposit(depositAmountBN, 0, args)
  const resp = await tx.wait()
  //console.log("deposit resp", resp)
  OpenWaitDialog = false
  OpenReloadDlg = true
}

async function withdraw() {
  const provider = new ethers.providers.Web3Provider(window.ethereum)
  const signer = provider.getSigner()
  const poolContract = new ethers.Contract(PoolAddress, ABI, provider).connect(signer)
  //console.log("HereC")
  if(UnlockTime > Date.now()/1000) {
    const depositTime = formatTime(1000*(UnlockTime-3600))
    const unlockTime = formatTime(1000*UnlockTime)
    WarnText = `You just deposit some coins at ${depositTime}, now your coins are locked. Please retry after ${unlockTime}`
    OpenWarnDlg = true
    return
  }
  //console.log("HereD")
  if(UserCoins*1.15 < WithdrawAmount) { // WithdrawAmount is too big
    WarnText = `The value you want withdraw (${WithdrawAmount}) is larger than the maximum possible value (${UserCoins}).`
    OpenWarnDlg = true
    return
  }
  var withdrawShare = UserShare // default is full withdraw
  if(UserCoins*1.0 > WithdrawAmount/*partial withdraw*/) {
    withdrawShare = UserShare * WithdrawAmount / UserCoins
  }
  if(withdrawShare > UserShare) {
    withdrawShare = UserShare
  }
  //console.log("HereE")
  const withdrawShareBN = ethers.utils.parseUnits((withdrawShare*1.0).toFixed(ShareDecimal), ShareDecimal)
  OpenWaitDialog = true
  const tx = await poolContract.withdraw(withdrawShareBN, {gasPrice: DefaultGasPrice})
  const resp = await tx.wait()
  //console.log("withdraw resp", resp)
  OpenWaitDialog = false
  OpenReloadDlg = true
}

