async function fetchLogs(startHeight, toHeight, provider, filter, countLimit, step) {
  var allLogs = []
  while(toHeight > startHeight) {
    filter.fromBlock = Math.max(toHeight-step+1, startHeight)
    filter.toBlock = toHeight
    var logs = await provider.getLogs(filter)
    //console.log("aaa ", filter.fromBlock, filter.toBlock, logs.length)
    for(var i=logs.length-1; i>=0; i--) {
      //console.log("bnumber ", logs[i].blockNumber)
      allLogs.unshift(logs[i])
    }
    if(allLogs.length > countLimit) {
      break
    }
    toHeight -= step
    if(logs.length < 10000) {//try larger step
      step = step * 2
    }
  }
  return allLogs
}

function showPoolOperations(logs) {
  var poolHisEntries = []
  var totalInvest = 0
  for(var i=0; i<logs.length; i++) {
    var log = logs[i]
    const timestamp = ethers.BigNumber.from("0x"+log.data.substr(2+24+24))
    const timeStr = formatTime(1000*timestamp.toNumber())
    if(log.topics[0] == Deposit || log.topics[0] == Withdraw) {
      var entry = {timeStr}
      const amountBN = ethers.BigNumber.from(log.data.substr(0, 2+24))
      entry.amount  = myFormatUnits(amountBN, CoinDecimal)
      entry.isDeposit = log.topics[0] == Deposit
      entry.isWithdraw = log.topics[0] == Withdraw
      if(log.topics[0] == Deposit) {
        totalInvest += entry.amount*1.0
      } else if(log.topics[0] == Withdraw) {
        totalInvest -= entry.amount*1.0
      }
      entry.transactionHash = log.transactionHash
      poolHisEntries.push(entry)
    }
  }
  PoolHisEntries = poolHisEntries
  //var totalEarning = UserCoins - totalInvest
  //TotalEarning = totalEarning.toFixed(DisplayDigits)
  ShowPoolTable = true
}

async function showHistoryTable(startHeight, toHeight, provider) {
  var filter = {address: PoolAddress, topics: [[Win, Buy]]}
  var addr = SignerAddr
  if(OnlyMyLottery) {
    const signer = provider.getSigner()
    if(AddrForHistory) {
      addr = AddrForHistory
    }
    try {
      ethers.utils.getAddress(addr)
    } catch(e) {
      WarnText = "Invalid Address: "+ addr
      OpenWarnDlg = true
      return
    }
    const signerAddr32 = ethers.utils.hexZeroPad(addr, 32)
    filter.topics.push(signerAddr32)
  }
  //console.log("OnlyMyLottery", OnlyMyLottery, addr, filter)
  var step = OnlyMyLottery? 100000 : 30000
  var logs = await fetchLogs(startHeight, toHeight, provider, filter, RecCountLimit, step)
  //console.log("logs", logs)
  var start = Math.max(logs.length-RecCountLimit, 0)
  var entryList = []
  for(var i=start; i<logs.length; i++) {
    var log = logs[i]
    const timestamp = ethers.BigNumber.from("0x"+log.data.substr(2+24+24))
    const timeStr = formatTime(1000*timestamp.toNumber())
    var entry = {timeStr}
    if(log.topics[0] == Win || log.topics[0] == Buy) {
      const amountBN = ethers.BigNumber.from(log.data.substr(0, 42))
      entry.amount  = myFormatUnits(amountBN, CoinDecimal)
      const multiplierBN = ethers.BigNumber.from("0x"+log.data.substr(42, 8))
      entry.multiplier = ethers.utils.formatUnits(multiplierBN, 2)
      entry.isWin = log.topics[0] == Win
      entry.isBuy = log.topics[0] == Buy
      if(entry.isWin) {
        TotalWon += entry.amount
      } else if(entry.isBuy) {
        TotalBought += entry.amount
      }
    }
    entry.transactionHash = log.transactionHash
    entry.id = log.transactionHash+"-"+log.logIndex
    if(!LogIdSet.has(entry.id)) {
      entryList.unshift(entry)
      LogIdSet.add(entry.id)
    }
  }
  HistoryEntries = [].concat(entryList, HistoryEntries)
  ShowHistoryTable = true
  //console.log("HistoryEntries", HistoryEntries)
  ROI = (TotalWon*100/TotalBought).toFixed(2)
}

