function onFilterMethodChange() {
  if(FilterMethod == OldFilterMethod) {
    return
  }
  OldFilterMethod = FilterMethod
  //console.log("onFilterMethodChange", FilterMethod)
  if(FilterMethod == "All") {
    OnlyMyLottery = false
    AddrForHistory = null
    showAndUpdateHistoryTable()
  } else if(FilterMethod == "Mine") {
    OnlyMyLottery = true
    AddrForHistory = null
    HistoryEntries = []
    showAndUpdateHistoryTable()
  } else if(FilterMethod=="Specified") {
    OpenAddressDlg = true
  }
}

