async function endAuction() {
  const provider = new ethers.providers.Web3Provider(window.ethereum)
  const signer = provider.getSigner()

  const tokenContract = new ethers.Contract(ClkAddress, Erc20ABI, provider).connect(signer)
  const allowanceBN = await tokenContract.allowance(SignerAddr, PoolAddress)
  if(allowanceBN.lt(ethers.utils.parseUnits("1", 26))) {
    HintText = "You have not approved enough CLK to this lottery contract. Please appove and retry."
    ApprovingAddress = ClkAddress
    OpenApproveDlg = true
    return
  }

  const balanceBN = await tokenContract.balanceOf(SignerAddr)
  const balance = myFormatUnits(balanceBN, 18)
  //console.log("HERE", balanceBN, balance, ClkOfAuction)
  if(balance < ClkOfAuction) {
    WarnText = `You balance (${balance}) is less than the amount (${ClkOfAuction}) you need to pay for the auction!`
    OpenWarnDlg = true
    return
  }

  const catsluckContract = new ethers.Contract(PoolAddress, ABI, provider).connect(signer)
  OpenWaitDialog = true
  const tx = await catsluckContract.endDutchAuction()
  await tx.wait()
  OpenWaitDialog = false
  OpenReloadDlg = true
}

